.selection-area {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 10;
}
.selection-area > div {
  display: flex;
  padding: 1rem;
  flex-wrap: wrap;
  align-content: flex-start;
  border-bottom: 0.14rem solid black;
}

.selection-area > div:last-child {
  border-bottom: 0.14rem solid white;
}
